/* ---------------------------
Form
------------------------------ */
footer {

	input {
		padding: 20px;
		font-size: 1rem;
		margin: 0;
		border: 1px solid $grey2;
		display: block;
	}

	.submit {
		border: 0;
		background-color: $white;
		color: $black;
		margin: 10px 0 0;

		&:hover {
			background-color: $light;
		}
	}
}

.btn-outlined {
	color: $dark;
	background-color: $white;
	border: 1px solid $grey;
	padding: 6px 16px;
	border-radius: 5px;
	font-size: 0.9rem;

	&:hover {
		background-color: $light;
		border-color: $light;
	}
}



/* ---------------------------
Blog
------------------------------ */
h1.post-title {
	font-weight: $font-weight-thin;
	font-size: $font-size-extralarge;
	line-height: 4rem;
	padding-top: 0;
	margin-top: 0;
} 

article {
	margin-bottom: 4rem;

	h1 {
		margin: 0;
		padding: 0;
	}

	a {
		width: 100%; 
		display: block;
		color: $black;
		text-decoration: none;
	}

	time {
		color: $grey;
		font-family: $font-sans-serif;
	}

}

.post-content {
	img {
		max-width: 100%;
	}

	.img-grey-bg {
		text-align: center;
		background: $light url("/assets/bg-nature.jpg");
		background-repeat: no-repeat;
		background-size: auto;


		img {
			width: 80%;
			margin: 20px auto;
		}
	}
}

#share-buttons {
	a {
		margin-right: 10px;
	}
}

.article-links {
	margin-top: 50px;
	font-size: 0.9rem;
	color: $grey;

	&::before { 
  		content: "---";
  	}

  	ul {
  		list-style: none;
  		padding: 0;
  		margin: 0;
  	}

  	a {
  		color: $grey;
  	}

}


/* ---------------------------
Page - Work
------------------------------ */
.page-work {
	h4 {
		margin: 0;
	}
}









/* ---------------------------
Mediaquery
------------------------------ */
@media only screen
and (min-device-width : 320px)
and (max-device-width : 736px) {
	.col-1, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-10, .col-11, .col-12 {
	  width: 100% !important;
	}

	.right {
		text-align: left;
	}

	body, html {
		font-size: 24px !important;
		padding: 0 10px; 
	}

	header, footer {
		padding: 2rem 0;
	}

	header  { 
		.col-4 {
			margin-left: 0 !important;
		}
	}

	footer {
		a {
			display: block;
		}
	}

	h1.post-title {
		font-size: 48px;
		line-height: 66px;
	}

	blockquote {
		padding-left: 20px;
		line-height: 44px;
	}
}